<template>
  <div>
    <table class="table is-fullwidth">
      <thead>
        <tr>
          <th>Time</th>
          <th>Customer</th>
          <th>Type</th>
          <th>Total</th>
          <th>Action</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(labRequest, key) in labRequests" :key="key">
          <td>{{ formatTime(labRequest.order_time) }}</td>
          <td class="is-capitalized">
            {{ labRequest.patient.first_name + " " + labRequest.patient.last_name}}
          </td>
          <td >{{ type(labRequest) }}</td>
          <td>{{ labRequest.total_cost }}</td>
          <td>
            <a class="button is-link is-expandible" @click="activateModal(labRequest)">
              <span class="icon">
                <i class="fas fa-shopping-cart"></i>
              </span>
              <span>Checkout lab request</span>
            </a>
          </td>
        </tr>
      </tbody>
    </table>
    <labrequestcheckout-component></labrequestcheckout-component>
  </div>
</template>

<script>
import LabRequestCheckoutComponent from "@/components/LabRequestCheckoutComponent";

export default {
  name: "PaymentRequestListingComponent",
  created() {
    this.$store.dispatch(
      "fetchLabRequests", this.$store.state.authentication.inMemoryToken
    );
    let store = this.$store;
    this.$socket.on("create-labrequest", labRequest => {
      store.dispatch("addLabRequest", labRequest);
    })
  },
  methods: {
    activateModal(labRequest) {
      this.$store.dispatch('toggleLabRequestCheckoutModalOn', true);
      this.$store.dispatch('updateCurrentCheckoutLabRequest', labRequest);
    },
    formatTime(time) {
      var date = new Date(time);
      return date.toLocaleString()
    },
    type(labRequest) {
      if (labRequest.exams) {
        return "EXAM";
      } else {
        return "VACCINE"
      }
    }
  },
  computed: {
    labRequests() {
      return this.$store.getters.createdLabRequested.sort((labRequestA, labRequestB) => {
        if(labRequestA.order_time < labRequestA.order_time) {
          return -1;
        } else if(labRequestA.order_time > labRequestB.order_time) {
          return 1;
        } else {
          return 0;
        }
      });
    }
  },
  components: {
    "labrequestcheckout-component": LabRequestCheckoutComponent
  }
};
</script>