<template>
    <div>
        <div class="level">
            <div class="level-left">
                <button class="button is-link" @click="toggleCreateTestModal">
                    <span class="icon">
                        <i class="fas fa-plus"></i>
                    </span>
                    <span>Create lab request</span>
                </button>
            </div>
            <partnerpatientresultsearch-component></partnerpatientresultsearch-component>
        </div>
        <table class="table is-fullwidth">
            <thead>
                <tr>
                    <th>Time</th>
                    <th>Customer</th>
                    <th>Status</th>
                    <th>Actions</th>
                    <th></th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(labRequest, key) in labRequests" :key=key>
                    <td>{{ formatTime(labRequest.orderTime) }}</td>
                    <td class="is-capitalized">
                        {{ labRequest.patient.firstName + " " + labRequest.patient.lastName}}
                    </td>
                    <td 
                        :class="{ 'has-text-success': isSent(labRequest.status) }" 
                        class="is-capitalized">
                            {{ labRequest.status }}
                    </td>
                    <td>
                        <button class="button is-link">
                            View Request
                        </button>
                    </td>
                    <td>
                        <a class="button is-link is-expandible" 
                            @click="activateModal(labRequest)" 
                            :disabled="isSent(labRequest.status)">
                                <span class="icon">
                                    <i class="far fa-paper-plane"></i>
                                </span>
                                <span>Send</span>
                        </a>
                    </td>
                </tr>
            </tbody>
        </table>
        <partnerlabrequestform-component></partnerlabrequestform-component>
    </div>
</template>

<script>
import PartnerNewLabRequestComponent from "@/components/PartnerNewLabRequestComponent.vue";
import PartnerPatientResultSearchComponent from "@/components/PartnerPatientResultSearchComponent.vue";
import PartnerLabRequestFormComponent from "@/components/PartnerLabRequestFormComponent";

export default {
    name: "PartnerLabRequestsComponent",
    components: {
        "partnerpatientresultsearch-component": PartnerPatientResultSearchComponent,
        "partnerlabrequestform-component": PartnerLabRequestFormComponent
    },
    methods: {
        activateModal(labRequest) {
            this.$store.dispatch('toggleExamCheckoutModalOn', true);
            this.$store.dispatch('updateCurrentCheckoutLabRequest', labRequest);
        },
        formatTime(time) {
            var date = new Date(time);
            return date.toLocaleString()
        },
        toggleCreateTestModal() {
            this.$store.dispatch("toggleCreatePartnerTestModalOn", true);
        },
        isSent(status) {
            return status === "sent";
        }
    },
    mounted() {
        this.$store.dispatch("fetchPartnerLabRequests")
    },
    computed: {
        labRequests() {
            return this.$store.state.partner.labRequests;
        }
    }
}
</script>